.federated-login-page{
    overflow: hidden;
    .shared-banner{
        clip-path: polygon(0 0, 100% 0%, 100% 80%, 0% 100%);
        padding:12% 8%;
        position: relative;
    }
    .banner-img{
        content: url('../../../../assets/FederatedLogin/banner-img.png');
        position: absolute;
        width: 35%;
        right: 0;
        top:25%;
        border-radius: 10px;
    }
}

@media(max-width:1401px){
    .federated-login-page{
        .shared-banner{
            clip-path: polygon(0 0, 100% 0%, 100% 85%, 0% 100%);
            padding:12% 8% 10;
        }
        .banner-img{
            width: 37.5%;
            top:32.5%;
        }
    }
}

@media(max-width:991px){
    .federated-login-page{
        .shared-banner{
            clip-path: polygon(0 0, 100% 0%, 100% 87.5%, 0% 100%);
            padding:13.5% 10%;
        }
    }
}
@media(max-width:767px){
    .federated-login-page{
        .shared-banner{
            clip-path: polygon(0 0, 100% 0%, 100% 95%, 0% 100%);
            padding:125px 7.5% 50px;
        }
        .banner-img{
            position: relative;
            right: unset;
            top:unset;
            width:100%;
            margin-top: 35px;
        }
    }
}