.shared-trial-row{
    padding:6% 10%;
    margin:5% 0;
    background: linear-gradient(#3868d6, #1f3a7a);
    color:#fff;
    text-align: center;
    position: relative;
    h1{
        font-size: 35px;
    }
    p{
        font-size: 20px;
        margin:25px 0 40px;
    }
    a{
        border:1px solid #fff;
        color:#fff;
        font-size: 18px;
        padding: 15px 30px;
        border-radius: 27.5px;
    }
    .line{
        background: linear-gradient(rgba(255,255,255,0.25), rgba(255,255,255,0.25));
        width: 15%;
        position: absolute;
        height: 65px;
    }
    .line1{
        top:7.5%;
        left:0;
    }
    .line2{
        bottom:7.5%;
        right:0;
    }
}

@media(max-width:1401px){
    .shared-trial-row{
        padding:7% 10%; 
        h1{
            font-size: 27.5px;
        }
        p{
            font-size: 15px;
            margin:20px 0 35px;
        }
        a{
            font-size: 15px;
            padding: 13.5px 30px;
        }
        .line{
            width: 20%;
            height: 55px;
        }
        .line1{
            top:7.5%;
        }
        .line2{
            bottom:7.5%;
        }
    }
}

@media(max-width:767px){
    .shared-trial-row{
        padding:100px 7.5%; 
        h1{
            font-size: 22.5px;
        }
        a{
            font-size: 15px;
            padding: 13px 25px;
        }
        .line{
            width: 150px;
            height: 40px;
        }
        .line1{
            top:25px;
        }
        .line2{
            bottom:25px;
        }
    }
}

.shared-trial-row{
    a{
        display: inline-block;
        vertical-align: middle;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        -webkit-transition-property: color;
        transition-property: color;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    a:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: -1px;
        bottom: -1px;
        left: -1px;
        right: -1px;
        border-radius: 27.5px;
        background: #fff;
        -webkit-transform: scaleX(0);
        transform: scaleX(0);
        -webkit-transform-origin: 50%;
        transform-origin: 50%;
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
    a:hover, a:focus, a:active {
        color: #265bd6;
    }
    a:hover:before, a:focus:before, a:active:before {
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }
}