.federated-login-page .component .row1{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin:5% 0;
    padding:0 7.5%;
    .img{
        content: url('../../../../assets/FederatedLogin/img.png');
        width: 42.5%;
        transition: 0.35s ease-in-out;
        &:hover{
            transform: scale(1.035);
            filter: brightness(0.95);
        }
    }
    .col2{
        width: 42.5%;
    }
    h1{
        font-size: 35px;
    }
    .p{
        font-size: 18px;
        margin:15px 0 45px;
    }
}

@media(max-width:1401px){
    .federated-login-page .component .row1{
        .img{
            width: 45%;
        }
        .col2{
            width: 45%;
        }
        h1{
            font-size: 27.5px;
        }
        .p{
            font-size: 15px;
            margin:15px 0 35px;
        }
    }
}

@media(max-width:925px){
    .federated-login-page .component .row1{
        margin:25px 0;
        .img{
            width: 400px;
            max-width: 100%;
            margin:20px auto;
        }
        .col2{
            width: 100%;
            margin:20px auto;
        }
        h1{
            font-size: 20px;
        }
        .p{
            margin:15px 0 30px;
        }
    }
}

