//============================================
.federated-login-page .component .row3{
    margin:7.5% 10%;
    padding:3.5% 7.5%;
    border-radius: 15px;
    background: #f9f9f9;
    h1{
        font-size: 35px;
        text-align: center;
    }
    .p{
        font-size: 18px;
        margin:15px 0 45px;
        text-align: center;
    }
    .items3{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .item3{
        width: 42.5%;
        &:hover .icon{
            border-radius: 66% 34% 33% 67% / 66% 67% 33% 34%;
            svg{
                transform: scale(1.2);
            }
        }
    }
    .icon{
        transition: 0.35s ease-in-out;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background: #427cff;
        color:#fff;
        font-size: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        svg{
            transition: 0.3s ease-in-out;
        }
    }
    h6{
        font-size: 19px;
        margin:25px 0 20px;
    }
    .item3 p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .federated-login-page .component .row3{
        h1{
            font-size: 27.5px;
        }
        .p{
            font-size: 15px;
            margin:15px 0 40px;
        }
        .item3{
            width: 45%;
        }
        .icon{
            width: 65px;
            height: 65px;
            font-size: 40px;
        }
        h6{
            font-size: 16px;
            margin:15px 0;
        }
        .item3 p{
            font-size: 15px;
            line-height: 1.7;
        }
    }
}

@media(max-width:900px){
    .federated-login-page .component .row3{
        margin:45px 7.5%;
        padding:7.5%;
        .item3{
            width: 47.5%;
        }
    }
}
@media(max-width:550px){
    .federated-login-page .component .row3{
        margin:40px 0 0;
        padding:35px 7.5%;
        border-radius: 0;
        h1{
            font-size: 20px;
        }
        .p{
            font-size: 15px;
            margin:15px 0 20px;
        }
        .item3{
            width: 100%;
            margin:15px 0;
        }
        .icon{
            width: 60px;
            height: 60px;
            font-size: 37px;
        }
    }
    .federated-login-page .shared-trial-row{
        margin-top: 0;
    }
}

//========================================
.federated-login-page .component .row2{
    .item2{
        overflow: hidden;
        border-radius: 10px;
        transition: 0.35s ease-in-out;
        &:hover{
            transform: translateY(-10px);
        }
    }
    .item2:before {
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        bottom: 0;
        background: #427cff;
        height: 4px;
        -webkit-transition-property: left, right;
        transition-property: left, right;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
    .item2:hover:before{
        left: 0;
        right: 0;
    }
      
}

@media(max-width:767px){
    .federated-login-page .component .row2{
        .item2{
            &:hover{
                transform: translateY(0px);
            }
        }
    }
}